import styled from "styled-components";

import { colors, marginsCss, spacingMap } from "../styleguide";
import { Typography } from "../Typography";
import { pxToRem } from "../utils";

import { BadgeVariants, StyledBadgeProps } from "./Badge.types";

export const BadgeVariantIconColors: Record<BadgeVariants, string> = {
	default: `${colors.darkerGray}`,
	active: `${colors.teal}`,
	testing: `${colors.gold}`,
	blue: `${colors.midBlue}`,
	error: `${colors.red}`,
	warning: `${colors.yellow}`,
};

export const BadgeVariantBackgroundColors: Record<BadgeVariants, string> = {
	default: `${colors.lightGray}`,
	active: `${colors.teal}40`,
	testing: `${colors.gold}40`,
	blue: `${colors.midBlue}40`,
	error: `${colors.red}40`,
	warning: `${colors.yellow}40`,
};

export const StyledBadge = styled.span<StyledBadgeProps>`
	display: inline-flex;
	flex-direction: row;
	align-items: center;
	border-radius: 4px;
	width: fit-content;
	height: ${(props) => props.size === "small" ? "20px" : "24px"};
	background-color: ${(props) => props.variant && BadgeVariantBackgroundColors[props.variant]};
	${marginsCss};
`;

export const StyledBadgeText = styled(Typography) <Pick<StyledBadgeProps, "size">>`
	text-wrap: nowrap;
	padding: ${(props) => props.size === "small" ? `0 ${spacingMap.xxs}` : `${pxToRem(2)} ${spacingMap.xs}`};
`;
